import React, { useEffect, useState } from 'react';
import { motion } from 'framer-motion';

const Onas = () => {
  const [isVisible, setIsVisible] = useState(false);
  const scrollThreshold = 350; // Nastavte vku scrollovania

  useEffect(() => {
    const handleScroll = () => {
      const currentScrollY = window.scrollY;

      if (currentScrollY > scrollThreshold) {
        setIsVisible(true);
      } else {
        setIsVisible(false);
      }

    };

    window.addEventListener('scroll', handleScroll);

    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, []);

  return (
    <div>
      <div className='container-bg'>
        <h1 className='nadpis-onas' >N PRBEH</h1>
        <div className='container-text'>
          <p className='nas-pribeh-text' >
            Matokarov tm Skid-Kart sa zaal ako sen. Nadenci motorportu a zavodov mali jedno spolon - ich ve pre rchlos a saivos.
          </p>
          <p className='nas-pribeh-text' >
            Vetko sa zaalo v kartingovom centre zo sdlom vo Vekom arii, kde sa stretvali kad vkend.
          </p>
          <p className='nas-pribeh-text' >
            Po niekokch rokoch tvrdej prce, jazdenia a asti na rznych zvodoch sa sen konene stal skutonosou. Matokarov tm Skid-Kart vstpil na kartov scnu a zaal sai vo viacerch ligch a ampiontoch uestnench pod SWS.
          </p>
          <p className='nas-pribeh-text'>
            Ich prbeh je prkladom toho, ako tvrd prca, oddanos a ve mu premeni sen na realitu a ako zitky z rchlosti a sae mu spoji ud a vytvori trval priatestv. Matokarov tm Skid-Kart sa stal symbolem spechu a jednoty vo svete motorsportu.
          </p>
        </div>
      </div>
      <div className='container-onas'>
        <motion.h1 className='nadpis-jazdci'>SPOZNAJTE N TM</motion.h1>
        <h2 className='nadpis-pretekary'>NAI PRETEKRI</h2>
        <motion.div className='container-flex' animate={{ opacity: isVisible ? 1 : 0, y: isVisible ? 0 : -20 }} transition={{ duration: 2 }}>
          <div className='container-pretekar'>
            <img className='foto' src='https://5c28f2b7d4.cbaul-cdnwnd.com/8cedf03de1f32a976c055ceebd5d5628/200000021-7e63e7e640/700/366167814_1049814183092942_7951358282331849700_n.webp?ph=5c28f2b7d4' alt='kiko'></img>
            <p className='meno-jazdca'>KRISITIN</p>
            <p className='priezvisko-jazdca'>DUBOVSK</p>
            <p className='popis-jazdca'>Krisitin Dubovsk, talentovan pretekr tmu Skid-Kart, je znmy svojou rchlosou a precznym ovldanm karta.

              Jeho schopnos adaptova sa na rzne trate a podmienky ho rob silnm sperom na zvodnch okruhoch.

              Okrem svojich jazdeckch schopnost je Krisitin aj skvel taktik a tmov hr.

              Je schopn rchlo ta situcie na trati a reagova na ne, aby dosiahol najlep vkon.

              Jeho ve a odhodlanie prekona kad prekku ho robia obbenm medzi fanikmi a spermi.</p>
          </div>
          <div className='container-pretekar'>
            <img className='foto' src='https://5c28f2b7d4.cbaul-cdnwnd.com/8cedf03de1f32a976c055ceebd5d5628/200000025-0588405887/280383935_5045064992238738_3042260572115002211_n.webp?ph=5c28f2b7d4' alt=''></img>
            <p className='meno-jazdca'>JN</p>
            <p className='priezvisko-jazdca'>IMKO</p>
            <p className='popis-jazdca'>Jn imko, talentovan pretekr tmu Skid-Kart, je znmy svojou rchlosou a precznym ovldanm karta.

              Jeho schopnos adaptova sa na rzne trate a podmienky ho rob silnm sperom na zvodnch okruhoch.

              Okrem svojich jazdeckch schopnost je Jn aj skvel taktik a tmov hr.

              Je schopn rchlo ta situcie na trati a reagova na ne, aby dosiahol najlep vkon.

              Jeho ve a odhodlanie prekona kad prekku ho robia obbenm medzi fanikmi a spermi.</p>
          </div>
          <div className='container-pretekar'>
            <img className='foto' src='https://5c28f2b7d4.cbaul-cdnwnd.com/8cedf03de1f32a976c055ceebd5d5628/200000023-881688816a/393451816_6986597138058208_4894586330339984625_n.webp?ph=5c28f2b7d4' alt=''></img>
            <p className='meno-jazdca'>IMON</p>
            <p className='priezvisko-jazdca'>PIVOVARNIEK</p>
            <p className='popis-jazdca'>imon Pivovarniek, talentovan pretekr tmu Skid-Kart, je znmy svojou rchlosou a precznym ovldanm karta.

              Jeho schopnos adaptova sa na rzne trate a podmienky ho rob silnm sperom na zvodnch okruhoch.

              Okrem svojich jazdeckch schopnost je imon aj skvel taktik a tmov hr.

              Je schopn rchlo ta situcie na trati a reagova na ne, aby dosiahol najlep vkon.

              Jeho ve a odhodlanie prekona kad prekku ho robia obbenm medzi fanikmi a spermi.</p>
          </div>
        </motion.div>
      </div>
      <div className='footer-onas'>
        <p>SKID-KART</p>
      </div>
    </div>
  );
};

export default Onas;
